import React from "react";
const AdminDashboard = React.lazy(() =>
  import("./views/dashboard/AdminDashboard")
);
const ServiceProviderDashboard = React.lazy(() =>
  import("./views/dashboard/ServiceProviderDashboard")
);
const Profile = React.lazy(() => import("./views/profile/Profile"));
const ServicesList = React.lazy(() => import("./views/service/ServiceList"));
const ServicesAdd = React.lazy(() => import("./views/service/ServiceAdd"));
const ServicesEdit = React.lazy(() => import("./views/service/ServiceEdit"));

const ServiceProviderList = React.lazy(() =>
  import("./views/service-providers/ServiceProviderList")
);

const ServiceProviderDetails = React.lazy(() =>
  import("./views/service-providers/ServiceProviderDetails")
);

const Appointments = React.lazy(() =>
  import("./views/appointments/Appointments")
);

const ServiceProviderAppointments = React.lazy(() =>
  import("./views/appointments/ServiceProviderAppointments")
);

const AppointmentsDetails = React.lazy(() =>
  import("./views/appointments/AppointmentsDetails")
);

const Payments = React.lazy(() => import("./views/payments/Payments"));

const UsersList = React.lazy(() => import("./views/users/UsersList"));

const Cars = React.lazy(() => import("./views/cars/CarsList"));

const AddBrand = React.lazy(() => import("./views/cars/AddBrand"));
const EditBrand = React.lazy(() => import("./views/cars/EditBrand"));
const AddCar = React.lazy(() => import("./views/cars/AddCar"));
const EditCar = React.lazy(() => import("./views/cars/EditCar"));
const AddModel = React.lazy(() => import("./views/cars/AddModel"));
const EditModel = React.lazy(() => import("./views/cars/EditModel"));

//Service Provder
const ServiceProviderProfile = React.lazy(() =>
  import("./views/service-providers/ServiceProviderProfile")
);

//update  price
const UpdatePrices = React.lazy(() => import("./views/prices/UpdatePrices"));

//referrals
const Referrals = React.lazy(() => import("./views/referrals/Referrals"));

//states&area
const AddState = React.lazy(() => import("./views/state/AddState"));
const EditState = React.lazy(() => import("./views/state/EditState"));
const ListState = React.lazy(() => import("./views/state/ListState"));

const AddArea = React.lazy(() => import("./views/area/AddArea"));
const EditArea = React.lazy(() => import("./views/area/EditArea"));
const ListArea = React.lazy(() => import("./views/area/ListArea"));

const Feedback = React.lazy(() => import("./views/feedback/Feedback"));

//Advertisements
const AddAdvertisement = React.lazy(() =>
  import("./views/advertisements/AddAdvertisement")
);
const EditAdvertisement = React.lazy(() =>
  import("./views/advertisements/EditAdvertisement")
);
const AdvertisementList = React.lazy(() =>
  import("./views/advertisements/AdvertisementList")
);

const routes = [
  { path: "/", exact: true, name: "Home" },
  { path: "/admin-dashboard", name: "Dashboard", component: AdminDashboard },
  {
    path: "/service-provider-dashboard",
    name: "Dashboard",
    component: ServiceProviderDashboard,
  },

  {
    path: "/service-provider-appointments",
    name: "Bookings",
    component: ServiceProviderAppointments,
  },

  {
    path: "/service-provider-profile",
    name: "Profile",
    component: ServiceProviderProfile,
  },

  { path: "/profile", name: "Profile", component: Profile },
  { path: "/services", name: "Services", component: ServicesList },
  { path: "/add-service", name: "Add Service", component: ServicesAdd },
  { path: "/edit-service", name: "Edit Service", component: ServicesEdit },

  {
    path: "/service-providers",
    name: "Service Providers",
    component: ServiceProviderList,
  },
  {
    path: "/appointments",
    name: "Appointments",
    component: Appointments,
  },
  {
    path: "/application-details",
    name: "Appointments Details",
    component: AppointmentsDetails,
  },

  {
    path: "/service-provider-details",
    name: "Service Provider  Details",
    component: ServiceProviderDetails,
  },

  { path: "/users", name: "Users", component: UsersList },

  { path: "/payments", name: "Payments", component: Payments },

  { path: "/cars", name: "Cars", component: Cars },

  { path: "/add-brand", name: "Cars", component: AddBrand },
  { path: "/edit-brand", name: "Cars", component: EditBrand },
  { path: "/add-car", name: "Cars", component: AddCar },
  { path: "/edit-car", name: "Cars", component: EditCar },
  { path: "/add-model", name: "Cars", component: AddModel },
  { path: "/edit-model", name: "Cars", component: EditModel },

  { path: "/update-prices", name: "Update Prices", component: UpdatePrices },
  { path: "/referrals", name: "referrals", component: Referrals },

  { path: "/add-state", name: "State", component: AddState },
  { path: "/edit-state", name: "State", component: EditState },
  { path: "/list-state", name: "State", component: ListState },
  { path: "/edit-area", name: "Area", component: EditArea },
  { path: "/add-area", name: "Area", component: AddArea },
  { path: "/show-feedbacks", name: "Feedback", component: Feedback },

  { path: "/list-area", name: "Area", component: ListArea },

  {
    path: "/add-advertisement",
    name: "Advertisement",
    component: AddAdvertisement,
  },
  {
    path: "/edit-advertisement",
    name: "Advertisement",
    component: EditAdvertisement,
  },
  {
    path: "/advertisement-list",
    name: "Advertisement",
    component: AdvertisementList,
  },
];

export default routes;
